<template>
  <div class="install_guide">
    <Topheader></Topheader>
    <div class="guide_item">
      <div class="item_title">webpack</div>
      <div class="item_cont">
        webpack.config.js项目配置文件 entry入口文件entry: { //bundle1: './main.js',// 第一个JS文件 bundle2:
        './main1.js' // 第二个JS文件 output: { filename: '[name].js' / 输出文件命名} }, name对应entry中key-value中的key值
      </div>
      <div class="item_title">sass安装</div>
      <div class="item_cont">
        <span>npm install node-sass --save-dev</span>
        <br />
        <span>npm install sass-loader@7.3.1 --save-dev</span>
        <br />
        <span>c</span>
        <br />
        <span>webpack.base.conf.js中module.rules中插入{test:/\.scss$/,loaders:["style","css","sass"]}</span>
      </div>
      <div class="item_title">el-ui安装引入</div>
      <div class="item_cont">
        npm i element-ui -S
        <br />
        完整引入 zIndex 初始弹框的z-index
        import 'element-ui/lib/theme-chalk/index.css';
        import ElementUI from 'element-ui';
        Vue.use(Element, { size: 'small', zIndex: 3000 });
        按需引入
        import { Button } from 'element-ui';
        Vue.prototype.$ELEMENT = { size: 'small', zIndex: 3000 };
        Vue.use(Button);
      </div>
      <div class="item_title">vuex状态管理</div>
      <div class="item_cont">
        npm install --save vuex
        注入实例 main.js new Vue中注入store 调用$store
      </div>
      <div class="item_title">el-ui安装</div>
      <div class="item_cont"></div>
      <div class="item_title">el-ui安装</div>
      <div class="item_cont"></div>
    </div>
  </div>
</template>

<script>
import Topheader from "../../src/components/TopHeader";
export default {
  name: "homePage"
  ,components:{
      Topheader:Topheader
  }
};
</script>

<style lang="scss">
.install_guide {
  .guide_item {
    .item_title {
    }
  }
}
</style>



